.modalWrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.5);
    z-index: 99;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    background-color: white;
    box-shadow: 0 0 5px black;
    z-index: 100;
    padding: 10px 10px 40px 10px;
    @media (max-width: 500px) and (orientation: portrait) {
        width: 80%;
    }
    @media (max-width: 933px) and (orientation: landscape) {
        width: 80%;
        max-height: 95%;
    }
    & svg {
        position: absolute;
        top: 5px;
        right: 8px;
        font-size: 3rem;
        color: #64d9ce;
        transition: 0.3s;
        &:hover {
            transform: scale(1.2);
            cursor: pointer;
        }
    }
    &__title {
        color: #64d9ce;
        font-size: 3.5rem;
        font-weight: bold;
        text-align: center;
    }
    &__dialog {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 50px;
        @media (max-width: 933px) and (orientation: landscape) {
            margin-top: 25px;
        }
        &--box {
            flex-basis: 40%;
            font-size: 1.4rem;
            padding: 10px;
            @media (max-width: 800px) and (orientation: portrait) {
                flex-basis: 100%;
            }
            & .dialog {
                &__input {
                    width: 100%;
                    padding: 5px;
                }
            }
        }
        &--btn {
            margin: 100px auto 0 auto;
            width: 80%;
            @media (max-width: 500px) and (orientation: portrait) {
                margin-top: 50px;
            }
            @media (max-width: 933px) and (orientation: landscape) {
                margin-top: 40px;
            }
        }
    }
}
